import React from "react";
import AdminMenu from "../../components/AdminMenu/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import { useEffect, useState } from "react";
import { RiUser3Fill } from "react-icons/ri";
import { FcApproval } from "react-icons/fc";
import { FaIdCard } from "react-icons/fa";

import { BsPhoneFill } from "react-icons/bs";
import { MdEmail } from "react-icons/md";

import Nav from "../../components/Navbar/Nav";

const Users = () => {
  const [allUsers, SetallUsers] = useState([]);

  const getAllUsers = async () => {
    try {
      const { data } = await axios.get("/api/auth/getalluser");

      if (data?.success) {
        SetallUsers(data.users);
        toast.success(data.message);
      } else {
        toast.error(data.message);
      }
    } catch (error) {

      toast.error("something went wrong");
    }
  };

  useEffect(() => {
    getAllUsers();
  }, []);

  return (
    <div>
      <Nav />
      <div className="admin_main">
        <hr />
        <div className="d-flex row container-fluid p-3 ">
          <div className="col-lg-2 col-sm-12">
            <AdminMenu />
          </div>
          <div className="col-lg-10 ps-5  ">
            <div className=" p-3 w-75 ps-5 ">
              <h1 className="text-uppercase mb-3 mt-0 fw-bold   font-monospace">
                all users{" "}
              </h1>
              <div>
                <div className="d-flex row gap-3">
                  {allUsers.map((data) => (
                    <>
                      <div className="card" style={{ width: "18rem" }}>
                        <div className="card-body" key={data._id}>
                          <h3 className="card-text text-capitalize ">
                            <RiUser3Fill className="me-1 " />{" "}
                            {data.userName || data.fullname}
                          </h3>
                          <hr />

                          <p>
                            {" "}
                            <MdEmail className="me-1" />
                            {data.email}
                          </p>
                          <p>
                            {" "}
                            <BsPhoneFill className="me-1" /> {data.phone}
                          </p>
                          <p>
                            {data?.ROLE === 0 ? (
                              <>
                                <RiUser3Fill className="me-1 mb-3" />
                                <span className="fw-bold text-muted ">
                                  Normal User
                                </span>{" "}
                              </>
                            ) : (
                              <>
                                <FcApproval className="me-1 fs-3  " />
                                <span className="fw-bold text-uppercase  text-bg-warning rounded-4 p-1 ">
                                  advanced user
                                </span>{" "}
                              </>
                            )}{" "}
                          </p>
                          <hr />
                          <p>
                            {" "}
                            <FaIdCard className="me-1" />
                            {data._id}
                          </p>
                        </div>
                      </div>
                    </>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Users;
